import { useState } from 'react';
import { ApolloClient, ApolloProvider, InMemoryCache, useQuery, gql } from '@apollo/client';
import { Input } from 'antd';
import SearchResultsLoading from './SearchResultsLoading';
import SearchResultsError from './SearchResultsError';
import SearchResultsLoaded from './SearchResultsLoaded';

import './Search.scss';

const client = new ApolloClient({
  uri: `${process.env.REACT_APP_API_URL}/graphql`,
  cache: new InMemoryCache({
    typePolicies: {
      items: {
        //  Cf.https://www.apollographql.com/docs/react/caching/cache-configuration/#generating-unique-identifiers
        keyFields: []
      }
    }
  })
});

const buildQuery = query => gql`
  query {
    items {
      house(search: "${query}", limit: 100) {
        id,
        house_name,
        city,
        latitude,
        longitude,
        construction_date,
        construction_date_numeric,
        abandon_date,
        abandon_date_numeric,
        complete_plan,
        house_dimensions,
        cistern,
        images {
          id,
          directus_files_id {
            id
          }
        }
      }
    }
  }`;

const Search = props => {

  const [ query, setQuery ] = useState();

  return (
    <div className="search">
      <div className="search-bar">
        <Input.Search 
          placeholder="Search..." 
          allowClear 
          size="large"
          onSearch={value => setQuery(value)} />
      </div>
      
      <div className="search-results">
        <ApolloProvider client={client}>
          { query && <SearchResults query={query} page={1} /> }
        </ApolloProvider>
      </div>
    </div>
  )

}

function SearchResults(props) {

  const { loading, error, data } = useQuery(buildQuery(props.query));

  if (loading)
    return <SearchResultsLoading />
  else if (error)
    return <SearchResultsError />
  else 
    return <SearchResultsLoaded data={data.items.house} />

}

export default Search;
